	module.exports = {"data":{"markdownRemark":{"html":"<h3>System parameters</h3>\n<p>Elasticsearch version and more basic parameters:</p>\n<pre><code class=\"language-bash\">curl -XGET https://localhost:9200 -u admin:admin --insecure\n</code></pre>\n<p>Information on nodes:</p>\n<pre><code class=\"language-bash\">curl -XGET https://localhost:9200/_cat/nodes?v -u admin:admin --insecure\n</code></pre>\n<p>Integrated plugins:</p>\n<pre><code class=\"language-bash\">curl -XGET https://localhost:9200/_cat/plugins?v -u admin:admin --insecure\n</code></pre>\n<p>Information on authentication:</p>\n<pre><code class=\"language-bash\">curl -XGET https://localhost:9200/_opendistro/_security/authinfo -u admin:admin --insecure\n</code></pre>\n<p>Information on cluster:</p>\n<pre><code class=\"language-http\">http://localhost:9200/_cluster/settings?include_defaults=true\n</code></pre>\n<p>Indices:</p>\n<pre><code class=\"language-bash\">curl -XGET https://localhost:9200/_cat/indices -u admin:admin --insecure\n</code></pre>\n<h3>Cloning an index from dev tools console:</h3>\n<pre><code>POST _reindex\n{\n  \"source\": {\n    \"index\": \"portefeuille\"\n  },\n  \"dest\": {\n    \"index\": \"portefeuille_test_1\"\n  }\n}\n</code></pre>\n<h3>Creating a sample index from dev tools console:</h3>\n<pre><code>DELETE /usersdata\n</code></pre>\n<pre><code>PUT /usersdata/user/1\n{ \"age\": 42, \"balance\": 12000 }\nPUT /usersdata/user/2\n{ \"age\": 28, \"balance\": 7000 }\nPUT /usersdata/user/3\n{ \"age\": 51, \"balance\": 2300 }\nPUT /usersdata/user/4\n{ \"age\": 15, \"balance\": 450 }\nPUT /usersdata/user/5\n{ \"age\": 33 }\nPUT /usersdata/user/6\n{ \"age\": 32 }\nPUT /usersdata/user/7\n{ \"age\": 27 }\nPUT /usersdata/user/8\n{ \"age\": 79 }\nPUT /usersdata/user/9\n{ \"age\": 43, balance: null }\n</code></pre>\n<pre><code>GET /usersdata\n</code></pre>\n<pre><code>GET /usersdata/_search\n{\n    \"query\": {\n        \"match_all\": {}\n    }\n}\n</code></pre>\n<h3>Painless language scripts</h3>\n<pre><code>(((ctx?._source[\"SURF_PARC\"]?:0)?:0)/((ctx?._source[\"Bovin\"]?:1)?:1)?:1)\n</code></pre>","frontmatter":{"title":"Useful Queries","date":"December 03, 2020","path":"/elk-useful-queries","tags":["Elastic Search","queries","environment variables","system"],"excerpt":""}}},"pathContext":{"prev":{"html":"<h3>Setting up Zookeeper &#x26; Kafka Broker</h3>\n<pre><code class=\"language-bash\">cd kafka_2.13-2.6.0/bin\n</code></pre>\n<p>Setting Up Kafka: follow instructions from <a href=\"https://github.com/dilipsundarraj1/kafka-for-developers-using-spring-boot/blob/master/SetUpKafka.md\">github</a>.</p>\n<pre><code class=\"language-bash\"></code></pre>","id":"/home/nicolas/Documents/workshop/src/pages/2020-11-20-kafka/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2020-11-20T11:12:18.123Z","path":"/kafka","title":"Kafka","excerpt":"","tags":["Kafka","Java","Spring Boot","Spring 5"]}},"next":null}}

